.loginPage{
	display:grid;
	place-items:center;
	background: linear-gradient(220.55deg, #B7DCFF 0%, #FFA4F6 100%);
	height: 100vh;
	width: 100vw;
	position: fixed;
	left: 0;
	top: 0;
}
.loginPage .back{
	position: absolute;
	left: 10px;
	top: 10px;
	font-size: 30px;
	color: hotpink;
}
.loginBox,.registBox{
	background-color: #F5F5F5;
	padding: 20px;
	display:grid;
	width: 80%;
	grid-template-rows:60px repeat(2,50px) 40px 40px;
	grid-row-gap:15px;
	place-items:center stretch;
	transition: 0.2s;	
}
.loginTitle{
	text-align:center;
}
.loginPage p{
	display:grid;
	grid-template-columns:30px auto;
	place-items:center;
	border-bottom:1px solid silver;
}
.loginPage .lab{
	grid-area:1/1/2/2;
	font-size:20px;
	color: silver;
}

.loginPage .input{
	padding: 10px;
	outline: none;
	border:none;
	height: 40px;
	min-width: 200px;
	background-color: transparent;
	justify-self: start;
}
.loginPage .loginBtn{
	height: 35px;
	width: 60%;
	place-self: center;
	border: none;
	outline: none;
	background-color: hotpink;
	border-radius: 50px;
	color: white;
	margin-top: 20px;
}
.loginPage .link{
	margin-top: 10px;
	text-align: center;
	font-size: 13px;
	align-self: start;
}